.slider{
  width: 100%;
  height: 100%;
  padding: 40px 0;
  background: rgba($color: #000, $alpha: .9);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 2rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99999;
  &_image_container{
    height: 100%;
    animation: appear .25s ease-out;
    position: relative;
    overflow: hidden;
    cursor: zoom-in;
  }
  &_image{
    height: 100%;
    transition: height .2s;
    &-zoom{
      @extend .slider_image;
      height: 250%;
      position: relative;
      z-index: 99990;
    }
  }
  &_button{
    border: none;
    background: transparent;
    fill: $white-dark;
    font-size: 2rem;
    outline: none;
    cursor: pointer;
    transition: fill .1s, transform .1s;
    &:hover{
      fill: $white-light;
    }
    &-left, &-right{
      @extend .slider_button;
      width: 33%;
      height: 100%;
      padding: 20px;
      svg{
        height: 20px;
        transition: .1s;
      }
      &:hover{
        svg{
          transform: scale(1.3)
        }
      }
    }
    &-left{
      text-align: left;
    }
    &-right{
      text-align: right;
    }
    &-close, &-zoom-close{
      width: 20px;
      height: 20px;
      padding: 0;
      border: unset;
      background: unset;
      fill: $white-dark;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 25px;
      right: 25px;
      z-index: 99998;
      &:hover{
        fill: rgb(230, 47, 47);
        transform: scale(1.2)
      }
    }
    &-zoom-close{
      fill: rgb(230, 47, 47);
      display: none;
      top: 18px;
      right: 18px;
      cursor: zoom-out;
      &-showed{
        @extend .slider_button-zoom-close;
        display: flex;
        box-sizing: content-box;
      }
    }
  }
  &_counter{
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $font-primary;
    font-size: 1rem;
    position: absolute;
    left: calc(50% - 35px);
    bottom: 5px;
  }
}

@media (max-width: 1024px) {
  .slider{
    padding: 50px 0;
  }
}

@media (max-width: 768px) {
  .slider{
    justify-content: center;
    &_image_container{
      width: 80%;
      height: auto;
      pointer-events: none;
      .slider_button-zoom-close-showed{
        display: none;
      }
    }
    &_image{
      width: 100%;
      height: auto;
      &-zoom{
        transform: scale(1);
      }
    }
    &_button{
      padding: 10px;
      position: absolute;
      &-left, &-right{
        min-width: 50%;
        height: calc(100% - 100px);
        padding: 0 20px 100px;
        top: 100px;
      }
      &-left{
        left: 0;
      }
      &-right{
        right: 0;
      }
      &-close{
        padding: unset;
      }
    }
    &_counter{
        width: 80px;
        font-size: 1.25rem;
        left: calc(50% - 40px);
        bottom: 10px;
    }
  }
}

@media (max-width: 568px) {
  .slider{
    padding: 34px;
    &_image_container{
      width: 100%;
    }
    &_button{
      font-size: 1.5rem;
    }
    &_counter{
      width: 100px;
      font-size: 1.25rem;
      left: calc(50% - 50px);
      bottom: 20px;
    }
  }
}

@media (max-width: 425px) {
  .slider{
    &_button{
      &-left, &-right{
        padding: 0 10px 100px;
        svg{
          height: 16px;
        }
      }
      &-close{
        width: 16px;
        height: 16px;
        top: 30px;
        right: 30px;
      }
    }
  }
}

@media (min-width: 1440px) {
  .slider{
    padding: 50px 0;
    &_button{
      &-left, &-right{
        padding: 40px;
      }
    }
    &_counter{
      width: 100px;
      font-size: 1.5rem;
      left: calc(50% - 50px);
      bottom: 10px;
    }
  }
}

@media (min-width: 1920px) {
  .slider {
    padding: 70px 0;
  }
}
