.nav{
  width: 100%;
  height: 60px;
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $white;
  font-weight: bolder;
  z-index: 99991;
  overflow-x: hidden;
  &_logo{
    height: calc(100% - 20px);
    z-index: 99991;
  }
  &_button_container{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    :first-child{
      margin-left: 0;
    }
    &-showed{
      @extend .nav_button_container;
    }
    &-hidden{
      @extend .nav_button_container;
    }
  }
  &_button{
    height: 100%;
    margin: 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $white-light;
    text-decoration: none;
    position: relative;
    &:hover{
      color: $white-dark;
    }
    &-reservation{
      @extend .nav_button;
      height: 40px;
      padding: 10px 14px;
      border-radius: 20px;
      background: $black-light;
      color: white;
      margin-right: 0;
      transition: color .2s, background-color .2s;
      &:hover{
        color: $black;
        background: $white;
      }
    }
    &-open, &-close{
      border: unset;
      background: unset;
      display: none;
      color: $white-light;
      font-size: 24px;
      cursor: pointer;
      outline: unset
    }
    &-open{
      width: 40px;
      height: 100%;
      z-index: 99991;
      #menu_icon{
        width: 28px;
        height: 28px;
        fill: $white-light;
        overflow-x: hidden;
        overflow-y: auto;
        &:hover{
          fill: $white-dark;
        }
        .line-one, .line-two, .line-three{
          transition: transform .3s;
        }
      }
      &-cross{
        @extend .nav_button-open;
        #menu_icon{
          .line-one{
            transition-delay: .2s;
            transform: translateX(20px) translateY(-10px) rotate(45deg);
          }
          .line-two{
            transform: translateX(100%);
          }
          .line-three{
            transform-origin: left bottom;
            transition-delay: .2s;
            transform: translateX(20px) translateY(10px)  rotate(-45deg);
          }
        }
      }
    }
  }
  .contact{
    display: none;
  }
}


@media (max-width: 425px) {
  .nav{
    padding: 0 25px;
    &_button_container{
      width: 80%;
    }
    &_button{
      width: 100%;
      height: 50px;
      margin: 0;
      padding: 0 20px;
      transition: background-color .1s, color .1s;
      &-open, &-close{
        display: block;
      }
      &-close{
        position: absolute;
        top: 13px;
        right: 13px;
      }
    }
  }
}

@media (max-width: 768px) {
  .nav{
    &_button_container{
      width: 100%;
      height: fit-content;
      padding: 60px 0 0;
      box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.5);
      background: $black-dark;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      text-align: center;
      position: absolute;
      top: 0;
      right: 0;
      z-index: 99990;
      transform: translateX(100%);
      &-showed{
        animation: openMenu .25s ease-out forwards;
      }
      &-hidden{
        animation: closeMenu .25s ease-in forwards;
      }
    }
    &_button{
      width: 100%;
      height: 50px;
      margin: 0;
      padding: 0 20px;
      transition: background-color .1s, color .1s;
      &-reservation{
        height: 50px;
        margin: 0;
        border-radius: unset;
        padding: 0 20px;
        background: $black;
      }
      &-open, &-close{
        display: block;
      }
      &-close{
        font-size: 26px;
        position: absolute;
        top: 15px;
        right: 35px;
      }
    }
    .contact{
      margin: 20px auto;
      display: grid;
      position: relative;
      bottom: unset;
      right: unset;
      &_link{
        font-size: 30px;
      }
      .email{
        font-size: 24px;
      }
      a{
        background: unset;
      }
    }
  }
}

@media (min-width: 1440px) {
  .nav{
    height: 70px;
    &_button{
      font-size: 1.2rem;
    }
  }
}

@media (min-width: 1920px){
  .nav{
    height: 80px;
    &_button{
      font-size: 1.25rem;
    }
  }
}


@keyframes openMenu {
  0%{
    transform: translateY(-100%);
  }
  100%{
    transform: translateY(0);
  }
}

@keyframes closeMenu {
  0%{
    transform: translateY(0);
  }
  100%{
    transform: translateY(-100%);
  }
}
