.section-fotos{
  .image_container{
    width: 100%;
    height: fit-content;
    padding: 40px 12vh;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 40px;
  }
  .image{
    width: 100%;
    position: relative;
    cursor: pointer;
    opacity: .5;
    transition: opacity .1s;
    &:hover{
      opacity: 1;
    }
    &-principal{
      width: 100%;
    }
    &-cover{
      width: 100%;
      height: 100%;
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

@media (min-width: 1440px) {
  .section-fotos{
    padding-top: 5vh;
    padding-bottom: 5vh;
    .image_container{
      padding: 0 8vh;
      gap: 5vh;
    }
  }
}

@media (max-width: 1024px) {
  .section-fotos{
    padding-top: 5vh;
    padding-bottom: 5vh;
    .image_container{
      padding: 0 8vh;
      grid-template-columns: repeat(3, 1fr);
      gap: 5vh;
    }
  }
}

@media (max-width: 768px) {
  .section-fotos{
    padding-top: 4vh;
    padding-bottom: 4vh;
    .image_container{
      padding: 0 6vh;
      gap: 4vh;
      .image{
        opacity: 1;
      }
    }
  }
}

@media (max-width: 568px) {
  .section-fotos{
    .image_container{
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

@media (max-width: 425px) {
  .section-fotos{
    width: 100%;
    margin: 0;
    .image_container{
      grid-template-columns: 1fr;
    }
  }
}
