.section-precios{
  padding: 0 30px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.2rem;
  .caption{
    margin: 0;
    font-family: $font-primary;
    font-size: 2rem;
  }
  .contact{
    position: relative;
    right: unset;
    bottom: unset;
    &_link{
      font-size: 30px;
    }
    .email{
      font-size: 26px;
    }
  }
  .oferta{
    &_container{
      margin-top: 30px;
      width: 100%;
      height: fit-content;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      justify-items: center;
      align-items: center;
    }
    &_card{
      width: 300px;
      height: 400px;
      border-radius: 20px;
      padding: 20px;
      background: radial-gradient(at top left, $black, $black-light);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      color: $white-dark;
    }
    &_title{
      margin-bottom: 0;
      font-size: 1.5rem;
      font-family: $font-primary;
    }
    &_precio{
      margin: 0 10px 0 0;
      color: $white-light;
      font-family: $font-primary;
      font-size: 6rem;
    }
    &_list{
      width: 100%;
      margin: 0;
      padding-inline-start: 20px;
      text-align: left;
      font-size: 1.25rem;
      list-style-type: none;
      &_item{
        margin: 10px 0;
        position: relative;
        &::after{
          content: "";
          width: .5rem;
          height: .5rem;
          border-radius: .25rem;
          background-color: $blue;
          position: absolute;
          top: calc(50% - .25rem);
          left: -20px;
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  .section-precios{
    width: 100%;
    margin: 0;
    .oferta{
      &_container{
        margin-bottom: 20px;
        gap: 20px;
      }
      &_card{
        width: 100%;
        height: 350px;
      }
      &_precio{
        font-size: 5rem;
      }
      &_list{
        font-size: 1.1rem;
      }
    }
  }
}

@media (max-width: 768px) {
  .section-precios{
    font-size: 1.1rem;
    .text{
      margin: 30px 0;
    }
    .oferta{
      &_container{
        grid-template-columns: repeat(2, 1fr);
        grid-template-areas:"oferta_2 oferta_2"
                            "oferta_1 oferta_3";
        :nth-child(1){
          grid-area: oferta_1;
        }
        :nth-child(2){
          grid-area: oferta_2;
        }
        :nth-child(3){
          grid-area: oferta_3;
        }
      }
      &_card{
        width: 250px;
        height: 320px;
      }
      &_title{
        font-size: 1.4rem;
      }
      &_list{
        font-size: 1rem;
      }
    }
  }
}

@media (max-width: 568px) {
  .section-precios{
    .oferta{
      &_container{
        grid-template-columns: 1fr;
        grid-template-areas: unset;
        :nth-child(1){
          grid-area: unset;
        }
        :nth-child(2){
          grid-area: unset;
        }
        :nth-child(3){
          grid-area: unset;
        }
      }
    }
  }
}

@media (min-width: 1440px) {
  .section-precios{
    .contact{
      &_link{
        font-size: 36px;
      }
      .email{
        font-size: 32px;
      }
    }
    .oferta{
      &_container{
        max-width: 1200px;
        margin: 50px auto 0;
      }
      &_card{
        width: 340px;
        height: 450px;
        border-radius: 30px;
      }
    }
  }
}

@media (min-width: 1920px) {
  .section-precios{
    .caption{
      margin: 30px 0;
    }
    .oferta{
      &_container{
        max-width: 1300px;
        margin: 60px auto 0;
        align-self: center;
      }
      &_card{
        width: 375px;
        height: 500px;
        border-radius: 30px;
      }
    }
  }
}
